class MemoryUsageTest {
  async runMemoryTest(): Promise<void> {
    console.log('\nüíæ ÂÜÖÂ≠ò‰ΩøÁî®ÊµãËØï...')
    
    // Ëé∑ÂèñÂàùÂßãÂÜÖÂ≠ò‰ΩøÁî®
    const initialMemory = this.getMemoryUsage()
    console.log(`ÂàùÂßãÂÜÖÂ≠ò‰ΩøÁî®: ${(initialMemory.usedJSHeapSize / 1024 / 1024).toFixed(2)} MB`)
    
    // ÊâßË°åÂ§ßÈáèÊìç‰Ωú
    const testData = TestDataGenerator.generateCardData(1000)
    const startTime = performance.now()
    
    const createdCards: any[] = []
    for (const cardData of testData) {
      const result = await localOperationServiceOptimized.createCard(cardData)
      if (result.success && result.id) {
        createdCards.push(result)
      }
    }
    
    // Ëé∑ÂèñÂ≥∞ÂÄºÂÜÖÂ≠ò‰ΩøÁî®
    const peakMemory = this.getMemoryUsage()
    console.log(`Â≥∞ÂÄºÂÜÖÂ≠ò‰ΩøÁî®: ${(peakMemory.usedJSHeapSize / 1024 / 1024).toFixed(2)} MB`)
    
    // Ê∏ÖÁêÜÊï∞ÊçÆ
    for (const card of createdCards) {
      await db.cards.delete(card.id)
    }
    
    // Ëé∑ÂèñÊ∏ÖÁêÜÂêéÂÜÖÂ≠ò‰ΩøÁî®
    const finalMemory = this.getMemoryUsage()
    console.log(`Ê∏ÖÁêÜÂêéÂÜÖÂ≠ò‰ΩøÁî®: ${(finalMemory.usedJSHeapSize / 1024 / 1024).toFixed(2)} MB`)
    
    const memoryIncrease = peakMemory.usedJSHeapSize - initialMemory.usedJSHeapSize
    console.log(`ÂÜÖÂ≠òÂ¢ûÈïø: ${(memoryIncrease / 1024 / 1024).toFixed(2)} MB`)
    
    const duration = performance.now() - startTime
    console.log(`ÂàõÂª∫1000Âº†Âç°ÁâáËÄóÊó∂: ${duration.toFixed(2)}ms`)
    console.log(`Âπ≥ÂùáÊØèÂº†Âç°ÁâáËÄóÊó∂: ${(duration / 1000).toFixed(2)}ms`)
    
    // È™åËØÅÂÜÖÂ≠ò‰ΩøÁî®ÁõÆÊ†á
    if (memoryIncrease < 50 * 1024 * 1024) { // 50MB
      console.log('‚úÖ ÂÜÖÂ≠ò‰ΩøÁî®Â¢ûÈïø < 50MB: ËææÊ†á')
    } else {
      console.log(`‚ùå ÂÜÖÂ≠ò‰ΩøÁî®Â¢ûÈïø < 50MB: Êú™ËææÊ†á (ÂÆûÈôÖ: ${(memoryIncrease / 1024 / 1024).toFixed(2)}MB)`)
    }
  }
  
  private getMemoryUsage() {
    if ('memory' in performance) {
      return (performance as any).memory
    }
    return {
      usedJSHeapSize: 0,
      totalJSHeapSize: 0,
      jsHeapSizeLimit: 0
    }
  }
}

