class ConcurrencyTest {
  async runConcurrencyTest(): Promise<void> {
    console.log('\n‚ö° Âπ∂ÂèëÊÄßËÉΩÊµãËØï...')
    
    const concurrentOperations = 50
    const testData = TestDataGenerator.generateCardData(concurrentOperations)
    
    const startTime = performance.now()
    
    // Âπ∂ÂèëÊâßË°åÂàõÂª∫Êìç‰Ωú
    const promises = testData.map(cardData => 
      localOperationServiceOptimized.createCard(cardData)
    )
    
    const results = await Promise.all(promises)
    const duration = performance.now() - startTime
    
    const successCount = results.filter(r => r.success).length
    const averageTime = duration / concurrentOperations
    
    console.log(`Âπ∂ÂèëÂàõÂª∫${concurrentOperations}Âº†Âç°ÁâáËÄóÊó∂: ${duration.toFixed(2)}ms`)
    console.log(`Âπ≥ÂùáÊØèÂº†Âç°ÁâáËÄóÊó∂: ${averageTime.toFixed(2)}ms`)
    console.log(`ÊàêÂäüÁéá: ${(successCount / concurrentOperations * 100).toFixed(1)}%`)
    
    // Ê∏ÖÁêÜÊµãËØïÊï∞ÊçÆ
    for (const result of results) {
      if (result.success && result.id) {
        await db.cards.delete(result.id)
      }
    }
    
    // È™åËØÅÂπ∂ÂèëÊÄßËÉΩÁõÆÊ†á
    if (averageTime <= 100) {
      console.log('‚úÖ Âπ∂ÂèëÊìç‰ΩúÂπ≥ÂùáÊó∂Èó¥ < 100ms: ËææÊ†á')
    } else {
      console.log(`‚ùå Âπ∂ÂèëÊìç‰ΩúÂπ≥ÂùáÊó∂Èó¥ < 100ms: Êú™ËææÊ†á (ÂÆûÈôÖ: ${averageTime.toFixed(2)}ms)`)
    }
  }
}

