class SmallDatasetOptimizationTest {
  async runSmallDatasetTest(): Promise<void> {
    console.log('\nüìä Â∞èÊï∞ÊçÆÈõÜ‰ºòÂåñÊµãËØï...')
    
    // ÂàõÂª∫Â∞èÊï∞ÊçÆÈõÜÊµãËØïÊï∞ÊçÆ (9 cards, 8 folders, 13 tags)
    const smallCards = TestDataGenerator.generateCardData(9)
    const createdCards: any[] = []
    
    console.log('ÂàõÂª∫Â∞èÊï∞ÊçÆÈõÜÊµãËØïÊï∞ÊçÆ...')
    for (const cardData of smallCards) {
      const result = await localOperationServiceOptimized.createCard(cardData)
      if (result.success && result.id) {
        createdCards.push(result)
      }
    }
    
    console.log(`‚úÖ ÂàõÂª∫‰∫Ü ${createdCards.length} Âº†ÊµãËØïÂç°Áâá`)
    
    // ÊµãËØïÂ∞èÊï∞ÊçÆÈõÜÊü•ËØ¢ÊÄßËÉΩ
    const queryTestResults = await this.testQueryPerformance(createdCards)
    
    // ÊµãËØïÂÜÖÂ≠òÁºìÂ≠òÊïàÊûú
    const cacheTestResults = await this.testCacheEffectiveness(createdCards)
    
    // ÊµãËØïÊêúÁ¥¢ÊÄßËÉΩ
    const searchTestResults = await this.testSearchPerformance(createdCards)
    
    // Ê∏ÖÁêÜÊµãËØïÊï∞ÊçÆ
    console.log('Ê∏ÖÁêÜÂ∞èÊï∞ÊçÆÈõÜÊµãËØïÊï∞ÊçÆ...')
    for (const card of createdCards) {
      await db.cards.delete(card.id)
    }
    
    // ËæìÂá∫ÁªºÂêàÁªìÊûú
    console.log('\nüìà Â∞èÊï∞ÊçÆÈõÜ‰ºòÂåñÊµãËØïÁªìÊûú:')
    console.log(`Êü•ËØ¢ÊÄßËÉΩ: ${queryTestResults.averageQueryTime.toFixed(2)}ms (ÁõÆÊ†á: < 10ms) ${queryTestResults.passed ? '‚úÖ' : '‚ùå'}`)
    console.log(`ÁºìÂ≠òÂëΩ‰∏≠Áéá: ${(cacheTestResults.hitRate * 100).toFixed(1)}% (ÁõÆÊ†á: > 90%) ${cacheTestResults.passed ? '‚úÖ' : '‚ùå'}`)
    console.log(`ÊêúÁ¥¢ÊÄßËÉΩ: ${searchTestResults.averageSearchTime.toFixed(2)}ms (ÁõÆÊ†á: < 15ms) ${searchTestResults.passed ? '‚úÖ' : '‚ùå'}`)
    
    const allPassed = queryTestResults.passed && cacheTestResults.passed && searchTestResults.passed
    console.log(`\nÂ∞èÊï∞ÊçÆÈõÜ‰ºòÂåñÊÄª‰Ωì: ${allPassed ? '‚úÖ ÈÄöËøá' : '‚ùå Êú™ÈÄöËøá'}`)
    
    return allPassed
  }
  
  private async testQueryPerformance(cards: any[]): Promise<{
    averageQueryTime: number
    passed: boolean
  }> {
    console.log('ÊµãËØïÂ∞èÊï∞ÊçÆÈõÜÊü•ËØ¢ÊÄßËÉΩ...')
    
    const queryTimes: number[] = []
    const testIterations = 20
    
    for (let i = 0; i < testIterations; i++) {
      const startTime = performance.now()
      
      try {
        await localOperationServiceOptimized.getCards({ limit: 9 })
        const queryTime = performance.now() - startTime
        queryTimes.push(queryTime)
      } catch (error) {
        console.warn(`Êü•ËØ¢ÊµãËØï ${i + 1} Â§±Ë¥•:`, error)
      }
    }
    
    const averageQueryTime = queryTimes.reduce((sum, time) => sum + time, 0) / queryTimes.length
    const passed = averageQueryTime < 10 // ÁõÆÊ†á: Â∞è‰∫é10ms
    
    console.log(`Âπ≥ÂùáÊü•ËØ¢Êó∂Èó¥: ${averageQueryTime.toFixed(2)}ms`)
    
    return { averageQueryTime, passed }
  }
  
  private async testCacheEffectiveness(cards: any[]): Promise<{
    hitRate: number
    passed: boolean
  }> {
    console.log('ÊµãËØïÂ∞èÊï∞ÊçÆÈõÜÁºìÂ≠òÊïàÊûú...')
    
    let cacheHits = 0
    const totalQueries = 15
    
    // È¶ñÊ¨°Êü•ËØ¢ (ÁºìÂ≠òÊú™ÂëΩ‰∏≠)
    await localOperationServiceOptimized.getCards({ limit: 9 })
    
    // ÈáçÂ§çÊü•ËØ¢ (ÊµãËØïÁºìÂ≠òÂëΩ‰∏≠)
    for (let i = 0; i < totalQueries; i++) {
      const startTime = performance.now()
      
      try {
        await localOperationServiceOptimized.getCards({ limit: 9 })
        const queryTime = performance.now() - startTime
        
        // Â¶ÇÊûúÊü•ËØ¢Êó∂Èó¥Â∞è‰∫é1msÔºåËÆ§‰∏∫ÊòØÁºìÂ≠òÂëΩ‰∏≠
        if (queryTime < 1) {
          cacheHits++
        }
      } catch (error) {
        console.warn(`ÁºìÂ≠òÊµãËØï ${i + 1} Â§±Ë¥•:`, error)
      }
    }
    
    const hitRate = cacheHits / totalQueries
    const passed = hitRate > 0.9 // ÁõÆÊ†á: Â§ß‰∫é90%
    
    console.log(`ÁºìÂ≠òÂëΩ‰∏≠Áéá: ${(hitRate * 100).toFixed(1)}%`)
    
    return { hitRate, passed }
  }
  
  private async testSearchPerformance(cards: any[]): Promise<{
    averageSearchTime: number
    passed: boolean
  }> {
    console.log('ÊµãËØïÂ∞èÊï∞ÊçÆÈõÜÊêúÁ¥¢ÊÄßËÉΩ...')
    
    const searchTerms = ['test', 'card', 'content', 'title', 'back']
    const searchTimes: number[] = []
    
    for (const term of searchTerms) {
      const startTime = performance.now()
      
      try {
        await localOperationServiceOptimized.searchCards({
          term,
          limit: 10
        })
        const searchTime = performance.now() - startTime
        searchTimes.push(searchTime)
      } catch (error) {
        console.warn(`ÊêúÁ¥¢ÊµãËØïÂ§±Ë¥•:`, error)
      }
    }
    
    const averageSearchTime = searchTimes.reduce((sum, time) => sum + time, 0) / searchTimes.length
    const passed = averageSearchTime < 15 // ÁõÆÊ†á: Â∞è‰∫é15ms
    
    console.log(`Âπ≥ÂùáÊêúÁ¥¢Êó∂Èó¥: ${averageSearchTime.toFixed(2)}ms`)
    
    return { averageSearchTime, passed }
  }
}

